html {
	opacity: 0;
}



.magic {
	opacity: 0;

	perspective: 500px;
 	 perspective-origin: 66% 50%;

	transform:
		translateY(30px)
		perspective( 1200px ) rotateX(45deg);

		;



	transform-origin: 0px 600px;
}

#middle {
	min-height: 18vh;
	position:relative;
	.max-central {
		position:absolute;
		top: 10%;
		width: 100%;
		bottom: 10%;
		left: 0;
		right: 0;
	}

}
.quotes {
	


}
.quotes ul {
	position: relative;
	
	list-style-type: none;
	width: 100%;
	padding: 0;
	margin: 0;
	li {
		opacity: 0;
		padding: 0;
		position: absolute;
		top: 0;
		margin: auto;
		text-align: center;
		width: 100%;
		left: 0;
		right: 0;

		&:first-of-type {
			opacity: 1;
		}

		h4 {
			border-bottom: 1px solid #000;
			display: inline-block;
			font-size: 0.5;
			font-weight: normal;
			margin: 0;
		}

		h3 {
			margin: .2em 0 0 0;


		}



	}
}